<template>
  <div class="loginImg">
    <h3 class="title"></h3>
    <el-form
      :model="registerForm"
      :rules="rules"
      ref="registerForm"
      label-position="right"
      label-width="80px"
      class="register-container"
    >
      <el-form-item prop="account" label="">
        <el-input
          v-model="registerForm.account"
          @keyup.enter.native="registerSubmit"
          type="number"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="checkPass" label="">
        <el-input
          type="password"
          v-model="registerForm.checkPass"
          @keyup.enter.native="registerSubmit"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="name" label="">
        <el-input
          type="text"
          v-model="registerForm.name"
          @keyup.enter.native="registerSubmit"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="idCard" label="">
        <el-input
          type="text"
          v-model="registerForm.idCard"
          @keyup.enter.native="registerSubmit"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="address" label="">
        <el-input
          type="text"
          v-model="registerForm.address"
          @keyup.enter.native="registerSubmit"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click.native.prevent="registerSubmit"> </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      registerForm: {
        account: "",
        checkPass: "",
        name: "",
        idCard: "",
        address: ""
      },
      rules: {
        account: [{ required: true, message: "", trigger: "blur" }],
        checkPass: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }],
        idCard: [{ required: true, message: "", trigger: "blur" }],
        address: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    registerSubmit() {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          this.axios
            .post("/api/register", {
              phone: this.registerForm.account,
              pwd: this.registerForm.checkPass,
              idnumber: this.registerForm.idCard,
              name: this.registerForm.name,
              address: this.registerForm.address
            })
            .then(res => {
              if (res.data["state"] == 0) {
                //
                this.$message.success("");
                this.GLOBAL.headBarState = 1;
                this.$router.push("/login");
              } else {
                this.$message.error("" + res.data["state"]);
              }
            })
            .catch(err => {
              this.$message.error("");
            });
          this.$router.push("/login");
        }
      });
    }
  }
};
</script>


<style lang="stylus" scoped>
.loginImg {
  width: 400px;
  margin: 5% auto;
  padding: 40px 50px 30px 30px;
  border: 1px solid #909199;
  border-radius: 12px;
  text-align: center;
}

.title {
  margin-left: 10px;
  margin-bottom: 30px;
}

.registerButton {
  margin-left: 80%;
  color: red;
}

.register-container {
  width: 100%;
  margin: 0 auto;
}
</style>
