<template>
  <el-container class="container">
    <el-header class="sub-header">
      <div>
        <h2></h2>
      </div>
    </el-header>

    <el-main class="sub-main">
      <el-form ref="form" :model="form" :rules="rules" label-width="90px" style="text-align:left;">
        <el-form-item label="" prop="name">
          <el-input class="inputBox" placeholder="" v-model="form.name" clearable></el-input>
        </el-form-item>

        <el-form-item label="" prop="price">
          <el-input class="inputBox" placeholder="" v-model="form.price" clearable type="number"></el-input>
        </el-form-item>

        <el-form-item label="ISBN" prop="isbn">
          <el-input class="inputBox" placeholder="ISBN" v-model="form.isbn" clearable></el-input>
        </el-form-item>

        <el-form-item label="" prop="number">
          <el-input class="inputBox" placeholder="" v-model="form.number" clearable type="number"></el-input>
        </el-form-item>

        <el-form-item label="" prop="author">
          <el-input class="inputBox" placeholder="" v-model="form.author" clearable></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="form.class" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input
            class="textarea"
            type="textarea"
            resize="none"
            rows="6"
            placeholder=""
            v-model="form.detail"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-upload
            class="upload-demo"
            ref="uploadscan"
            drag
            list-type="picture"
            action="/api/upload"
            :auto-upload="false"
            :on-success="handleSuccess"
            :on-change="handleFileChange"
            :on-remove="handleFileRemove"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              
              <em></em>
            </div>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-row>
            <el-button type="warning" @click="submitForm('form')" round id="submit-button"> </el-button>
          </el-row>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>



<script>
export default {
  name: "NewBook",
  data() {
    return {
      form: {
        class: "",
        name: "",
        price: "",
        isbn: "",
        number: "",
        author: "",
        detail: "",
        picture: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        price: [{ required: true, message: "", trigger: "blur" }],
        number: [
          { required: true, message: "", trigger: "blur" }
        ],
        author: [{ required: true, message: "", trigger: "blur" }]
      },
      curFileNum: 0
    };
  },
  methods: {
    submitForm(form) {
      console.log("submit!!!");
      this.$refs[form].validate(valid => {
        if (valid && this.curFileNum > 0) {
          this.$refs.uploadscan.submit();
        } else if (valid) {
          //
          this.submitText();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    submitText() {
      this.axios
        .post("/api/publish", this.form)
        .then(res => {
          console.log("form post");
          console.log(res.data["state"]);
          if (res.data["state"] == 0) {
            //
            //
            this.$message({
              message: "",
              type: "success"
            });
            this.$refs.form.resetFields(); //
            this.form.detail = "";
            this.form.class = "";
          } else {
            console.log("form ");
            this.$message.error("");
          }
        })
        .catch(res => {
          console.log("catch error", res);
          this.$message.error("", res);
        });
    },
    handleFileRemove(file, fileList) {
      this.curFileNum = fileList.length;
    },
    handleFileChange(file, fileList) {
      this.curFileNum = fileList.length;
    },
    handleSuccess(res, file, filelist) {
      console.log("file upload success...");
      console.log(res);
      this.submitText();
      if (res["state"] == 0) {
        this.form.picture = res["picture"];
      } else {
        this.$message.error("");
      }
    }
  }
};
</script>


