<template>
  <el-container class="book-container">
    <div class="box-left">
      <div class="img-box">
        <img :src="getpic(orders.picture)" alt width="350px" style="margin-left:5px;max-height:350px;">
      </div>
    </div>
    <div class="box-middle">
      <div class="book-name">
        <span class="s-label"></span>
        <span class="s-value-h3">{{orders.orderid}}</span>
      </div>
      <div class="book-price">
        <span class="s-label">&emsp;</span>
        <span class="s-value-h3">{{orders.name}}</span>
      </div>

      <div class="book-author">
        <span class="s-label">&emsp;</span>
        <span class="s-value-h3">{{orders.author}}</span>
      </div>

      <div class="book-other">
        <span class="s-label"></span>
        <span class="s-value-h3">{{orders.time}}</span>
      </div>
      <div class="book-other">
        <span class="s-label"></span>
        <span class="s-value-h3 price">{{orders.total}}</span>
      </div>
      <div class="book-other">
        <span class="s-label"></span>
        <span class="s-value-h4">{{orders.state}}</span>
      </div>
      <div class="book-other">
        <span class="s-label">{{isBuyer? '':''}}</span>
        <span class="s-value-h4">{{orders.phone}}</span>
      </div>
      <br>
      <br>
      <div class="box-btn">
        <div class="finishOrder" v-if="isFnish">
          <el-button @click="finishOrder()"></el-button>
          <el-button @click="cancelOrder()" style="margin-left: 30px;"></el-button>
          <!-- <el-button @click="backHome">
            <strong></strong>
          </el-button>-->
        </div>
        <!-- <div class="unfinishOrder" v-if="!isFnish">
          <el-button @click="backHome">
            <strong></strong>
          </el-button>
        </div>-->
      </div>
    </div>
  </el-container>
</template>

<script>
import defaul_book from "../../../public/default_book.jpeg";

export default {
  name: "OrderDetail",
  data: () => ({
    isBuyer: null,
    orders: {
      orderid: "",
      name: "",
      author: "",
      time: "",
      picture: "",
      total: "",
      state: "",
      phone: ""
    }
  }),
  methods: {
    isFnish(item) {
      if (item.state === "" || item.state === "") return true;
      else return false;
    },
    backHome() {
      if (buyerornot === "True") this.$router.push("/buybook");
      else this.$router.push("/salebook");
    },
    finishOrder() {
      this.axios
        .post("/api/changestate", {
          oderid: this.orderid,
          orderstate: ""
        })
        .then(res => {
          if (res.data["state"] == 0) {
            //
            this.$message.success("");
          } else {
            this.$message.error(
              "" + res.data["state"]
            );
          }
        })
        .catch(err => {
          this.$message.error("");
        });
    },
    cancelOrder() {
      this.axios
        .post("/api/changestate", {
          oderid: this.orderid,
          orderstate: ""
        })
        .then(res => {
          if (res.data["state"] == 0) {
            //
            this.$message.success("");
          } else {
            this.$message.error("" + res.data["state"]);
          }
        })
        .catch(err => {
          this.$message.error("");
        });
    },
    getpic(pic) {
      if (pic.length > 0) {
        return "/show/" + pic;
      }
      return defaul_book;
    }
  },
  beforeMount() {
    var book = this.$route.params.book;
    var buyerornot = this.$route.params.buyerornot;
    if (buyerornot === "True") this.isBuyer = true;
    else this.isBuyer = false;
    console.log(book);
    console.log(buyerornot);
    console.log("234");
    this.orders.orderid = book.orderid;
    this.orders.name = book.name;
    this.orders.author = book.author;
    this.orders.time = book.time;
    this.orders.picture = book.picture;
    this.orders.total = book.total;
    this.orders.state = book.state;
    this.orders.phone = book.phone;
  }
};
</script>

